import _ from 'lodash'

import Table from '../../mixins/table'

import route from './route'

export default {
  name: route.key,

  mixins: [
    Table
  ],

  data() {
    return {
      action: null,
      list: null,
      filter: {
        draw_name: null,
        daterange: null,
        referenceno: null
      },
      isLoading: true,
      route,
    }
  },

  computed: {
    data() {
      let {
        draw,
        date_from,
        date_to,
        referenceno
      } = this.filter
      let search = this.search.trim().toUpperCase()
      let data = this.list ? this.list.data.filter(d => {
        return !search
          || (
               d.account_no.indexOf(search) > -1
            || d.username.trim().toUpperCase().indexOf(search) > -1
          )
      }) : []

      return this.sort.order ?
        _.orderBy(data, [this.sort.prop], [this.sort.order])
          : data
    },
  },

  mounted() {
    this.fetch()
  },

  methods: {
    async fetch() {
      try {
        this.list = await this.$store.dispatch('bet_history/list')
      } catch(e) {
        this.$message({
          type: 'error',
          message: e.message
        })
      }

      this.isLoading = false
    },
  }
}



